import productKindView from './product-kind-view';

/**
 * @description
 * Helpers for ext-bbm-product-summary-ng.
 *
 * @name Helpers
 * @type {Object}
 */
export default ({ stateContainer, intents }) => {
  /**
   * @name processKinds
   * @description
   * Processes the product kinds.
   *
   * @type {function}
   * @param {module:model-bb-product-summary-ng.ProductKind[]} productKinds ProductKinds to process.
   *
   * @returns {object[]}
   * @inner
   */
  const processKinds = productKinds => productKinds.map(productKindView);

  /**
   * @name loading
   * @description
   * Returns the loading state.
   * @type {function}
   *
   * @returns {boolean}
   * @inner
   */
  const loading = stateContainer.createSelector(state => state.productKinds.loading);

  /**
   * @name loadingError
   * @description
   * Returns the error state.
   * @type {function}
   *
   * @returns {boolean}
   * @inner
   */
  const loadingError = stateContainer.createSelector(state => state.productKinds.error);

  /**
   * @name getProducts
   * @description
   * Returns the raw products.
   * @type {function}
   *
   * @returns {module:model-bb-product-summary-ng.ProductKind[]}
   * @inner
   */
  const getProducts = stateContainer.createSelector(state => state.productKinds.data);

  /**
   * @name getProduct
   * @description Retrieves a product.
   * @type {function}
   *
   * @param {object} processedProduct
   * @returns {module:model-bb-product-summary-ng.Product} product
   * @inner
   */
  const getProduct = processedProduct => (
    getProducts()
      .find(productKind => productKind.id === processedProduct.kind)
      .products.find(product => product.id === processedProduct.id)
  );

  /**
   * @name Helpers#showProductDetails
   * @description Shows the product details view.
   * @type {function}
   *
   * @param {object} event
   * @param {string} productId
   */
  const showProductDetails = (event, productId) => {
    event.stopPropagation();

    intents.showProductDetails(productId);
  };

  /**
   * @name Helpers#selectProduct
   * @description
   * Sets the alternate workflow when a user selects a Product from the overview.
   *
   * @type {function}
   * @param {object} processedProduct
   */
  const selectProduct = processedProduct => {
    const product = getProduct(processedProduct);

    intents.showTransactions(product);
  };

  /**
   * @name Helpers#productKinds
   * @description
   * The list of the processed product kinds.
   * @type {function}
   *
   * @returns {object[]}
   */
  const getProcessedProducts = stateContainer.createSelector(({ productKinds }) => (
    productKinds.data ? processKinds(productKinds.data) : null
  ));

  /**
   * @name Helpers#hasProducts
   * @description Checks whether the list is empty.
   * @type {function}
   *
   * @returns {boolean} false if product list is empty.
   */
  const hasProducts = () => (
    Boolean(getProducts() && getProducts().length)
  );

  /**
   * @name Helpers#showLoadingState
   * @description Checks for the loading state.
   * @type {function}
   *
   * @returns {boolean}
   */
  const showLoadingState = () => (
    loading() && !getProducts()
  );

  /**
   * @name Helpers#showEmptyState
   * @description Checks if there are no products.
   * @type {function}
   *
   * @returns {boolean}
   */
  const showEmptyState = () => (
    Boolean(getProducts() && !getProducts().length) &&
    !loadingError() &&
    !loading()
  );

  /**
   * @name Helpers#showErrorState
   * @description Checks for the error state.
   * @type {function}
   *
   * @returns {boolean}
   */
  const showErrorState = () => (
    loadingError() && !loading()
  );

  return {
    getProcessedProducts,
    selectProduct,
    hasProducts,
    showProductDetails,
    showLoadingState,
    showEmptyState,
    showErrorState,
  };
};



