import { Intent } from './constants';

/**
 * @name Intents
 * @type {Object}
 */
export default ({ createRequest, handleRequest, service }) => ({
  /**
   * @name Intents#showProductDetails
   * @description The intent to show the product details.
   * @type {function}
   */
  showProductDetails: createRequest(Intent.SHOW_PRODUCT_DETAILS),

  /**
   * @name Intents#showTransactions
   * @description The intent to show the transactions.
   * @type {function}
   */
  showTransactions: createRequest(Intent.SHOW_TRANSACTIONS),

  /**
   * @name Intents#showProductSummary
   * @description Handler of the 'intent.bb.product.summary.show' intent.
   * @type {function}
   */
  showProductSummary: handleRequest(Intent.SHOW_PRODUCT_SUMMARY, () => {
    service.loadProducts();
  }),
});



